import React from 'react';

import styles from './Group.module.scss';

export interface GroupProps {
  /** Флаг видимости элемента */
  hidden?: boolean;

  /** ID для тестирования */
  dataTestId?: string;
}

/** Элемент, группирующий контролы редактора */
const Group: React.FC<GroupProps> = ({ hidden, children, dataTestId }) => (
  hidden
    ? null
    : <div className={ styles.group } data-test-id={ dataTestId }>{ children }</div>
);

export default Group;
